import Vue from "vue";
import Router from "vue-router";
import Inicio from "./views/Inicio.vue";
import Register from "./views/Register.vue";
Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "inicio",
      component: Inicio
    },
    {
      path: "/register",
      name: "register",
      component: Register
    },
    {
      path: "/programa",
      name: "programa",
      component: () => import("./views/Programa.vue")
    },
    {
      path: "/preçario",
      name: "precario",
      component: () => import("./views/Precario.vue")
    },
    {
      path: "/comunicaçoes",
      name: "comunicacoes",
      component: () => import("./views/ComLivres.vue")
    }
  ]
});
